.logo2 {
  position: fixed;
  right: calc((100% - 1060px) / 2);
  top: 99px;
  margin: 0;
  padding: 0;
  width: 200px;
  height: 200px;
  perspective: 500px;
  transform: translateX(120px);
  transition: all 250ms ease-out;
}
.logo2 .logo-inner {
  position: absolute;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transform: rotateX(-30deg) rotateY(45deg) translateX(0) translateY(70px) translateZ(-100px);
  transition: transform 200ms;
}
.logo2 img {
  position: absolute;
  top: 0;
  left: 0;
  padding: 20px;
  margin: 0;
  width: 100%;
  height: 100%;
  background: rgba(255,255,255,0.8);
  border: 1px dashed rgba(0,0,0,0.4);
  backface-visibility: hidden;
  transition: transform 0.4s ease-out;
}
.logo2 img.one {
  transform: rotateX(0);
  background: rgba(201,68,197,0.2);
  z-index: 6;
}
.logo2 img.two {
  transform: rotateX(180deg) translateZ(200px);
  background: rgba(201,68,197,0.2);
  z-index: 5;
}
.logo2 img.three {
  transform: rotateY(90deg) translateX(100px) translateZ(100px);
  background: rgba(201,68,197,0.2);
  z-index: 4;
}
.logo2 img.four {
  transform: rotateY(-90deg) translateX(-100px) translateZ(100px);
  background: rgba(201,68,197,0.2);
  z-index: 3;
}
.logo2 img.five {
  transform: rotateX(90deg) translateY(-100px) translateZ(100px);
  background: rgba(201,68,197,0.2);
  z-index: 2;
}
.logo2 img.six {
  transform: rotateX(-90deg) translateY(100px) translateZ(100px);
  background: rgba(201,68,197,0.2);
  z-index: 1;
}
@media screen and (max-width: 768px) {
  .logo2 {
    right: 0;
    top: 140px;
    position: relative;
    margin: 0 auto;
  }
}
